import React from 'react';
import { graphql } from 'gatsby';
import { Helmet } from 'react-helmet';
import { RouteComponentProps } from '@reach/router';

import { SiteMetadata } from 'interfaces/gatsby';

import { Page } from 'components/layout/Page';
import { Container } from 'components/layout/Container';
import { DocsWrapper } from 'components/docs/DocsWrapper';
import { PALDocsHeader } from 'components/docs/DocsHeader';
import { MarkdownContent } from 'components/page/Markdown';
import styled from 'styled-components';

import { FooterWrapper, PALFooter } from 'components/layout/Footer';
import { TocWrapper } from 'components/docs/TableOfContents';
import IndexLayout from 'layouts';
// import renderAst from 'utils/renderAst';
import { DocsContribution } from 'components/docs/DocsContribution';
import { BackToTopButton } from 'components/docs/BackToTopButton';

import BlogTableOfContent from 'components/page/blogs/blogTableOfContent';
import BlogTags from 'components/page/blogs/blogTags';
import BlogCategory from 'components/page/blogs/blogCategory';
import { Disqus, CommentCount } from 'gatsby-plugin-disqus';

interface BlogTemplateProps extends RouteComponentProps {
  data: {
    site: {
      siteMetadata: SiteMetadata;
    };
    markdownRemark: {
      html: string;
      tableOfContents: string;
      excerpt: string;
      frontmatter: {
        id: string;
        title: string;
        description?: string;
        prev?: string;
        next?: string;
        url?: string;
        tags?: string;
        categories?: [];
        createdby?: string;
        createdon?: Date;
      };
    };
  };
}

const BlogContainer = styled(Container)`
  display: flex;
  flex-direction: row;
  align-content: center;
  flex-wrap: wrap;
  // border: 1px solid black;
`;

const BlogContent = styled('div')`
  flex: 1 1 600px;
  padding: 20px 20px;
  flex-direction: column;
  //   border: 1px solid black;
`;

const BlogSidebar = styled('div')`
  // width: 250px;
  flex: 1 1 250px;
  // flex-direction: column;
  padding: 20px 20px;
  //  border: 1px solid black;
`;

const BlogTemplate: React.FC<BlogTemplateProps> = ({ data }) => {
  debugger;
  const [tocIsOpen, setTocIsOpen] = React.useState(false);
  const { markdownRemark, site, allMarkdownRemark } = data;
  const { siteMetadata } = site;

  const siteUrl = site.siteMetadata.siteUrl + '/' + markdownRemark.frontmatter.url;
  const DisqusTemplate = () => {
    let disqusConfig = {
      url: siteUrl,
      identifier: markdownRemark.frontmatter.id,
      title: markdownRemark.frontmatter.title
    };
    return <Disqus config={disqusConfig} />;
  };

  return (
    <IndexLayout navHidden>
      <Page docsPage>
        <Helmet>
          <title>
            {markdownRemark.frontmatter.title} &middot; {site.siteMetadata.title}
          </title>
          <meta name="description" content={markdownRemark.excerpt} />
          <meta property="og:title" content={markdownRemark.frontmatter.title} />
          <meta property="og:description" content={markdownRemark.excerpt} />
          <meta property="og:locale" content="en_US" />
          <meta property="og:type" content="website" />
          <meta property="og:site_name" content={siteUrl} />
          <meta name="twitter:card" content={siteUrl} />
          <meta name="twitter:description" content={site.siteMetadata.description} />
          <meta name="twitter:title" content={markdownRemark.frontmatter.title} />
          <meta property="og:url" content={siteUrl} />
        </Helmet>
        <DocsWrapper>
          <Container>
            <PALDocsHeader title={markdownRemark.frontmatter.title} subtitle={markdownRemark.frontmatter.description} />
            {/* <MarkdownContent>{renderAst(markdownRemark.html)}</MarkdownContent> */}
            <BlogContainer>
              <BlogContent>
                <MarkdownContent>
                  <div dangerouslySetInnerHTML={{ __html: markdownRemark.html }} />
                  <DisqusTemplate />
                </MarkdownContent>
              </BlogContent>
              <BlogSidebar>
                <BlogTableOfContent data={markdownRemark.tableOfContents} />
                <BlogTags data={markdownRemark.frontmatter.tags} />
                <BlogCategory data={allMarkdownRemark.edges} />
              </BlogSidebar>
            </BlogContainer>
            <FooterWrapper>
              <PALFooter
                version={siteMetadata.version}
                siteLastUpdated={siteMetadata.siteLastUpdated}
                socials={siteMetadata.socials}
              />
            </FooterWrapper>
          </Container>
          <BackToTopButton href="#" />
        </DocsWrapper>
      </Page>
    </IndexLayout>
  );
};

export default BlogTemplate;

export const query = graphql`
  query BlogTemplateQuery($slug: String!) {
    site {
      siteMetadata {
        title
        sidebarTitle
        sidebarSubtext
        siteLastUpdated
        description
        version
        siteUrl
        keywords
        author {
          name
          url
          email
        }
        socials {
          name
          imgpath
          url
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents
      excerpt
      frontmatter {
        id
        title
        description
        prev
        next
        url
        version
        tags
        createdby
        createdon
      }
    }
    allMarkdownRemark(filter: { fileAbsolutePath: { regex: "/blogs/" } }) {
      edges {
        node {
          frontmatter {
            category
          }
        }
      }
    }
  }
`;
