import React from 'react';
import styled from 'styled-components';

// import { space, colors, breakpoints } from 'utils/variables';
import { Heading } from 'components/foundations';

const BlogContainer = styled('div')`
  margin-top: 20px;
  padding: 10px 10px;
  // border: 0.5px solid black;
  // border-radius: 10px;

  & span {
    margin-right: 10px;
    padding: 5px 5px;
    // background-color: lightgray;
    border: 0.5px solid lightgray;
    border-radius: 5px;
    text-transform: uppercase;
    &:hover {
      background-color: lightgray;
    }
  }
`;

const BlogHeading = styled(Heading)`
  margin-bottom: 20px;
  text-decoration: underline;
  text-underline-position: under;
`;

const BlogTags = ({ data }) => {
  let tags = data.split(',');

  return (
    <BlogContainer>
      <BlogHeading size="500">Tags</BlogHeading>
      {tags.map((c) => {
        return <span>{c}</span>;
      })}
    </BlogContainer>
  );
};

export default BlogTags;
