import React from 'react';
import styled from 'styled-components';

// import { space, colors, breakpoints } from 'utils/variables';
import { Heading, Paragraph } from 'components/foundations';

const BlogHeading = styled(Heading)`
  margin-bottom: 20px;
  text-decoration: underline;
  text-underline-position: under;
`;

const BlogContainer = styled('div')`
  margin-top: 20px;
  padding: 10px 10px;
  // border: 0.5px solid black;
  // border-radius: 10px;
`;

const BlogTableOfContentContainer = styled('div')`
  & ul {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: flex-start;
    margin: 0;
    padding: 0;
    align-items: stretch;
    justify-content: space-between;
  }

  & li {
    display: block;
    flex: 1 1 auto; /* Default */
    border: 1px solid lightgray;
    margin-top: 2px;
    border-radius: 10px;
    // background: lightgray;
    width: 100%;
    padding 10px 10px;
  }

  & ul.li.a {
    list-style-type: dot;
    cursor: pointer !important;
  }
`;

const BlogTableOfContent = ({ data }) => {
  return (
    <BlogContainer>
      <BlogHeading size="500">Table of Content</BlogHeading>
      <BlogTableOfContentContainer dangerouslySetInnerHTML={{ __html: data }} />
    </BlogContainer>
  );
};

export default BlogTableOfContent;
