import * as React from 'react';
import { Heading, Box, Text } from 'components/foundations';
import { Space } from 'components/Theme';
import styled from 'styled-components';
import { colors } from 'utils/variables';
// import headerBackground from 'assets/images/svg/background/yellow1.svg';

interface DocsHeaderProps {
  title: string;
  subtitle?: string;
  margin?: Space;
}

const PALDocsHeader: React.FC<DocsHeaderProps> = ({ title, subtitle, margin }) => {
  return (
    <Section>
      {/* <img src={headerBackground} alt="" /> */}
      <Box as="header" mb={margin}>
        <Heading as="h1" size={800} color="white" fontFamily="sansSerif">
          {title}
        </Heading>
        <Text as="p" size={400} color="grey04" mt="sm">
          {subtitle}
        </Text>
      </Box>
    </Section>
  );
};

const Section = styled('section')`
  padding: 1.25rem 1.875rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 400px;
  //background-color: ${colors.grey07};
  // background-image: url(${headerBackground});
  // background-repeat: no-repeat;
  // background-origin: content-box;
  // background-image: -webkit-radial-gradient(circle, ${colors.red07}, ${colors.blue10});

  background-color: #f16fa1;
  // background-image: linear-gradient(83deg, #8b55f5, #f16fa1);
  background-image: linear-gradient(83deg, ${colors.red07}, ${colors.blue10});
  border-radius: 4px;
  color: #fff;
`;

PALDocsHeader.defaultProps = {
  subtitle: undefined,
  margin: 'xl'
};

export default PALDocsHeader;
