import React from 'react';
import styled from 'styled-components';

// import { space, colors, breakpoints } from 'utils/variables';
import { Heading } from 'components/foundations';

const BlogContainer = styled('div')`
  margin-top: 20px;
  padding: 10px 10px;
`;

const Span = styled('span')`
  text-transform: uppercase;
`;

const BlogHeading = styled(Heading)`
  margin-bottom: 20px;
  text-decoration: underline;
  text-underline-position: under;
`;

interface BlogCategories {
  CategoryName: string;
  Count: number;
}

const BlogCategory = ({ data }) => {
  let categories = []; // : BlogCategories[];
  data.map((node) => {
    let tags = node.node.frontmatter.category.split(',');
    tags.map((c) => {
      let cat: BlogCategories = {
        CategoryName: c,
        Count: 1
      };

      let indexOfCat = categories.findIndex((i) => i.CategoryName === cat.CategoryName);

      if (indexOfCat < 0) {
        categories.push(cat);
      } else {
        categories[indexOfCat].Count = categories[indexOfCat].Count + 1;
      }
    });
  });

  return (
    <BlogContainer>
      <BlogHeading size="500">Categories</BlogHeading>
      {categories.map((c) => {
        return (
          <div style={{ padding: '5px' }}>
            <Span>
              {c.CategoryName} ({c.Count}){' '}
            </Span>
          </div>
        );
      })}
    </BlogContainer>
  );
};

export default BlogCategory;
